.PublicationsPage {
    min-height: 85vh;
    text-align: center;
    padding: 32px 20px;
    padding-top: 6.5em;
    color: #eaf6ff;
    background: linear-gradient(180deg, rgba(255,255,255,0.02), transparent 30%);
}

.PublicationsHeader {
    max-width: 1100px;
    margin: 0 auto 1.2rem auto;
    display:flex;
    align-items:center;
    justify-content:space-between;
    gap:1rem;
}

.PublicationsList {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 22px;
    max-width: 1200px;
    margin: 0 auto;
    align-items: start;
}

.PublicationCard {
    background: linear-gradient(180deg, rgba(255,255,255,0.02), rgba(255,255,255,0.01));
    padding: 18px;
    border-radius: 12px;
    border: 1px solid rgba(255,255,255,0.04);
    box-shadow: 0 8px 30px rgba(6,10,18,0.6);
    transition: transform 360ms cubic-bezier(.2,.9,.3,1), box-shadow 360ms;
    transform: translateY(6px);
}
.PublicationCard:hover{ transform: translateY(-8px) scale(1.01); box-shadow: 0 28px 80px rgba(6,10,18,0.75); }

.PublicationCard .Title { display:flex; align-items:center; gap:12px; }
.publication-desc { color: var(--muted); margin-top: 0.6rem; font-size: 0.95rem; line-height:1.5 }

/* stagger reveal when using .reveal and data-reveal */
.PublicationCard { opacity: 0; transform: translateY(18px); }
.PublicationCard.is-revealed { opacity:1; transform: translateY(0); transition-delay: 0ms; }
.PublicationCard:nth-child(1).is-revealed { transition-delay: 60ms; }
.PublicationCard:nth-child(2).is-revealed { transition-delay: 120ms; }
.PublicationCard:nth-child(3).is-revealed { transition-delay: 180ms; }
.PublicationCard:nth-child(4).is-revealed { transition-delay: 240ms; }
