/* Resume.css */
.resume-container {
    text-align: center;
    padding: 20px;
}

.resume-container h2 {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 10px;
}

.resume-container p {
    font-size: 16px;
    color: #666;
    margin-bottom: 20px;
}

.download-btn {
    display: inline-block;
    background-color: #4CAF50;
    color: white;
    padding: 12px 20px;
    text-decoration: none;
    font-size: 18px;
    font-weight: bold;
    border-radius: 5px;
    transition: background 0.3s ease-in-out;
}

.download-btn:hover {
    background-color: #45a049;
}

/* Add micro-interaction to download button */
.download-btn{
    background: linear-gradient(90deg,var(--accent),var(--accent-2));
    color: #031018;
    box-shadow: 0 10px 36px rgba(0,0,0,0.5);
    transition: transform 220ms ease, box-shadow 220ms ease;
}
.download-btn:active{ transform: translateY(2px) scale(.995); }

