.PublicationsPage {
    min-height: 75vh;
    padding: 36px 22px;
    padding-top: 4.5em;
    color: #eaf6ff;
}

/* Projects uses the same card visuals as Publications */

.PublicationsList {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 20px;
    max-width: 1200px;
    margin: 0 auto;
}

.PublicationCard {
    background: linear-gradient(180deg, rgba(255,255,255,0.02), rgba(0,0,0,0.03));
    border-radius: 12px;
    padding: 18px;
    border: 1px solid rgba(255,255,255,0.03);
    box-shadow: 0 6px 28px rgba(4,8,12,0.6);
    transition: transform 320ms cubic-bezier(.2,.9,.3,1), box-shadow 320ms, opacity 320ms;
}
.PublicationCard:hover{ transform: translateY(-6px); box-shadow: 0 24px 80px rgba(4,8,12,0.8); }

.PublicationCard h3 { margin: 0 0 .4rem 0; color: #eaf6ff }
.PublicationCard p { color: var(--muted); margin: 0; }

.PublicationCard { opacity: 0; transform: translateY(18px); }
.PublicationCard.is-revealed { opacity:1; transform: translateY(0); }
