/* ContactMe.css */
body {
    font-family: Arial, sans-serif;
    width: 100%;
    color: wheat;
    background-color: #282c34;
    text-align: center;
  }
  
  .contact-container {
    padding-top: 5em;
    width: 100%;
  }

  .ContactContent {
    max-width: 720px;
    margin: 50px auto;
    padding: 28px;
    background: linear-gradient(180deg, rgba(255,255,255,0.02), rgba(0,0,0,0.02));
    border-radius: 14px;
    border: 1px solid rgba(255,255,255,0.03);
    box-shadow: 0 18px 48px rgba(0,0,0,0.55);
  }
  
  .contact-container h2 {
    font-size: 28px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .contact-container p {
    font-size: 14px;
    color: wheat;
    margin-bottom: 20px;
  }
  
  .input-group {
    margin-bottom: 15px;
    text-align: left;
  }
  
  .input-group label {
    display: block;
    font-weight: bold;
    margin-bottom: 5px;
  }
  
  .input-group input,
  .input-group textarea {
    width: 100% !important;
    padding-left: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
    border: 1px solid rgba(255,255,255,0.06);
    border-radius: 8px;
    background: rgba(255,255,255,0.02);
    color: #eaf6ff;
    font-size: 16px;
  }
  
  .input-group textarea {
    resize: none;
    height: 100px;
  }

  .submit-btn {
    width: 100%;
    padding: 12px 18px;
    background: linear-gradient(90deg,var(--accent),var(--accent-2));
    color: #031018;
    border: none;
    border-radius: 10px;
    font-size: 18px;
    cursor: pointer;
    font-weight: 700;
    box-shadow: 0 10px 30px rgba(0,0,0,0.45);
  }
  
  .submit-btn:hover {
    background: #45a049;
  }
  
  .submit-btn:focus {
    outline: none;
  }
  
@media (max-width: 820px) {
    .ContactContent {
        max-width: 70%;
        margin: 50px auto;
        padding: 20px;
        background: inherit;
        border-radius: 10px;
    }
    .submit-btn {
        width: 104.45%;
        padding: 10px;
        background: #4CAF50;
        color: white;
        border: none;
        border-radius: 5px;
        font-size: 18px;
        cursor: pointer;
      }

}